import React from 'react'
import styled from 'styled-components'

const InputComponent = ({ className, label, buttons = [], value, onChange }) => (
  <div className={className}>
    <div className="input__label">
      <p>{label}</p>
    </div>
    <input type="text" value={value} onChange={onChange} />
    {buttons}
  </div>
)

export const Input = styled(InputComponent)`
  display: flex;
  align-items: center;

  .input__label {
    transition: color 0.2s;
    p {
      font-weight: 500;
      display: inline-block;
      margin: 0;
      margin-right: 10px;
      padding: 5px 0;
      min-width: 100px;
    }
  }

  input {
    flex: 1 1 0;
    padding: 10px 12px;
    box-sizing: border-box;
    height: 38px;
    border: 1px solid #d4d4d4;
  }

  button {
    margin-left: 5px;
  }
`

export const ButtonComponent = ({ label, className, disabled, onClick }) => (
  <button disabled={disabled} className={className} onClick={onClick}>
    <span>{label}</span>
  </button>
)

export const Button = styled(ButtonComponent)`
  cursor: pointer;

  font-size: 12px;
  border-radius: 0px;
  padding: 6px 12px;
  min-width: 74px;
  min-height: 38px;
  transition: background-color 0.1s;
  border: 1px solid #d4d4d4;
  -webkit-appearance: none;

  &:focus {
    box-shadow: 0 0 0px 2px #8fc1ded9;
  }

  &:active,
  &:focus {
    outline: 0;
  }

  &:hover {
    background-color: #eee;
  }
`

export const ToggleSwitch = ({ onClick, isOn, label }) => (
  <div className={`${isOn ? 'switch switch--on' : 'switch switch--off'}`}>
    <div className="switch__label">
      <p onClick={onClick}>{label}</p>
    </div>
    <button className="switch__control" onClick={onClick}>
      <div className="switch__track" />
      <div className="switch__thumb" />
    </button>
  </div>
)

export const Message = ({ type, title, description }) => {
  return (
    <div className={(type && `message message--${type}`) || 'message'}>
      <h3 className="message__title">{title}</h3>
      <p className="message__description">{description}</p>
    </div>
  )
}
