import React from 'react'
import { connect } from 'react-redux'
import browser from 'webextension-polyfill'

import { ToggleSwitch, Message } from '../components/'
import { GlobalStyles } from './styled'

import { DEBUGGER, supportsRecorder } from '../recording/recorders/types'

import {
  initialize,
  startRecording,
  pauseRecording,
  resumeRecording,
  stopRecording,
  resetRecording,
} from './store/recording/actions'

import { RecordingState } from './store/recording/types'
import { isSameTab, isSameWindow } from '../utils/tabs'
import { viewTab } from './store/tabs/actions'
import { configure } from './store/settings/actions'
import { Spinner } from './Spinner'

const classes = (obj) =>
  Object.entries(obj)
    .filter(([, show]) => show)
    .map(([cls]) => cls)
    .join(' ')

const isLoading = (state) =>
  [RecordingState.INIT, RecordingState.STARTING, RecordingState.STOPPING].includes(state)

const RecordButton = ({ disabled, onClick }) => (
  <button disabled={disabled} className="btn btn--record" onClick={onClick}>
    <span>Start recording</span>
  </button>
)

const ViewHistoryButton = ({ onClick }) => (
  <button className="btn btn--history" onClick={onClick}>
    <span>View history</span>
  </button>
)

const StopButton = ({ disabled, active, onClick }) => {
  const className = classes({
    btn: true,
    'btn--record': true,
    'btn--record--active': active,
  })

  return (
    <button disabled={disabled} className={className} onClick={onClick}>
      <span>Stop</span>
    </button>
  )
}

const Button = ({ onClick, children }) => (
  <button className="btn" onClick={onClick}>
    <span>{children}</span>
  </button>
)

class Popup extends React.Component {
  constructor(props) {
    super(props)
  }

  componentDidMount() {
    this.props.dispatch(initialize())
  }

  onToggleDownloadFile = () => {
    this.props.dispatch(
      configure({
        downloadFile: !this.props.downloadFile,
      })
    )
  }

  onToggleClearCache = () => {
    this.props.dispatch(
      configure({
        clearCache: !this.props.clearCache,
      })
    )
  }

  onToggleCorrelateRequests = () => {
    this.props.dispatch(
      configure({
        correlateRequests: !this.props.correlateRequests,
      })
    )
  }

  start = () => {
    this.props.dispatch(
      startRecording({
        downloadFile: this.props.downloadFile,
        clearCache: this.props.clearCache,
        correlateRequests: this.props.correlateRequests,
        openIn: this.props.openIn,
      })
    )
  }

  pause = () => {
    this.props.dispatch(pauseRecording())
  }

  resume = () => {
    this.props.dispatch(resumeRecording())
  }

  stop = () => {
    this.props.dispatch(stopRecording())
  }

  reset = () => {
    this.props.dispatch(resetRecording())
  }

  viewRecordedTab = () => {
    this.props.dispatch(viewTab(this.props.recordedTab))
  }

  openOptionsPage = () => {
    browser.runtime.openOptionsPage()
  }

  renderControls() {
    const { recordingState, isRecording } = this.props

    if (!isRecording) {
      return (
        <>
          <RecordButton onClick={this.start} />
          <ViewHistoryButton onClick={this.openOptionsPage} />
        </>
      )
    }

    return (
      <div className="btn-row">
        <StopButton active={recordingState === RecordingState.STARTED} onClick={this.stop} />
        {recordingState === RecordingState.PAUSED ? (
          <Button onClick={this.resume}>Resume</Button>
        ) : (
          <Button onClick={this.pause}>Pause</Button>
        )}
        <Button onClick={this.reset}>Reset</Button>
      </div>
    )
  }

  renderMessage() {
    if (this.props.error) {
      const {
        title = 'An unknown error occured',
        message = 'Please check the developer console for details',
      } = this.props.error

      return <Message type="alert" title={title} description={message} />
    }

    switch (this.props.recordingState) {
      case RecordingState.INIT:
      case RecordingState.STOPPED:
        return (
          <div className="idle-display">
            <h2>No data recorded yet</h2>
            <p>Click Start recording to begin</p>
          </div>
        )

      case RecordingState.STARTING:
        return (
          <Message
            type="info"
            title="Starting everything up"
            description="We're doing loads of complex calculations. This should not take long!"
          />
        )

      case RecordingState.STOPPING:
        return (
          <Message
            type="info"
            title="Shutting things down"
            description="Waiting for everything to gracefully shutdown. It should be done any second now."
          />
        )

      case RecordingState.PAUSED:
      case RecordingState.STARTED:
        if (this.props.recordingInAnotherWindow) {
          return (
            <Message
              type="info"
              title="Recording in another window!"
              description="You have an active recording in another window."
            />
          )
        }

        if (this.props.recordingInAnotherTab) {
          return (
            <Message
              type="info"
              title="Recording in another tab!"
              description={
                <>
                  You have an active recording in{' '}
                  <a onClick={this.viewRecordedTab} href="#recording-tab">
                    another tab
                  </a>
                </>
              }
            />
          )
        }

        return (
          <Message
            type="info"
            title="You're now recording!"
            description="Everything happening in this tab will now be recorded until you stop or pause the recording."
          />
        )
    }
  }

  renderBody() {
    const { recordingState, isRecording } = this.props

    if (recordingState === RecordingState.INIT) {
      return (
        <div className="layout__body">
          <Spinner />
        </div>
      )
    }

    return (
      <div className="layout__body">
        {this.renderMessage()}
        {!isRecording && (
          <div className="options">
            <ToggleSwitch
              onClick={this.onToggleDownloadFile}
              isOn={this.props.downloadFile}
              label="Download HAR file"
            />
            <ToggleSwitch
              onClick={this.onToggleClearCache}
              isOn={this.props.clearCache}
              label="Clear cache (last 7 days)"
            />
            {supportsRecorder(DEBUGGER) && (
              <ToggleSwitch
                onClick={this.onToggleCorrelateRequests}
                isOn={this.props.correlateRequests}
                label="Correlate request/response data"
              />
            )}
          </div>
        )}
        {this.renderControls()}
        {isLoading(recordingState) && <Spinner />}
      </div>
    )
  }

  render() {
    return (
      <>
        <GlobalStyles />
        <div className="layout">
          <div className="layout__header">
            <div className="logo">
              <svg width="40px" height="40px" viewBox="0 0 40 40">
                <g stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                  <g fill="#7D64FF" fillRule="nonzero">
                    <path
                      d="M40,39.0697674 L0,39.0697674 L13.3154654,10.4762471 L21.3371967,16.411329 L31.8000724,0 L40,39.0697674 Z M25.3342992,32.8919165 L25.4204998,32.8919165 C26.4105301,32.8939273 27.3626447,32.5089059 28.0760594,31.8180465 C28.816664,31.1432125 29.2335132,30.1806802 29.2205723,29.1752911 C29.2536728,28.2059062 28.8491702,27.2735041 28.1202463,26.6389749 C27.5127027,26.025549 26.6937105,25.6712496 25.8333937,25.649673 L25.7681999,25.649673 C25.6559814,25.6494354 25.5442913,25.6651432 25.4364361,25.6963313 L27.5487142,22.5381475 L25.866715,21.3556511 L25.0699022,22.5381475 L23.0351322,25.6657118 C22.685259,26.1840564 22.3926114,26.641162 22.2122419,26.9531894 C22.0241902,27.2889838 21.8624068,27.6390072 21.7283593,28.0000851 C21.5757843,28.3835873 21.4971183,28.7928085 21.4965592,29.2059106 C21.4871219,30.1983202 21.8968286,31.1480867 22.6236871,31.8187755 C23.3278092,32.5090247 24.2724098,32.8944773 25.2553423,32.8926455 L25.3342992,32.8919165 Z M16.3911626,29.4909636 L18.7222021,32.8088064 L21.2140529,32.8088064 L18.4715683,28.9587674 L20.9083665,25.5541693 L19.2922854,24.4278087 L18.5737052,25.3755555 L16.3882651,28.4885392 L16.3882651,22.2414298 L14.2057226,20.4501884 L14.2057226,32.8102645 L16.3882651,32.8102645 L16.3882651,29.4909636 L16.3911626,29.4909636 Z M25.3371967,30.8192672 C24.4506606,30.8192672 23.7319812,30.0959636 23.7319812,29.2037235 C23.7319812,28.3114833 24.4506606,27.5881797 25.3371967,27.5881797 L25.3516842,27.5881797 C25.7786951,27.589121 26.1869324,27.7649424 26.4824339,28.0751758 C26.7936543,28.3628931 26.9694312,28.7696751 26.9663166,29.194975 C26.9571873,30.0941845 26.2307038,30.8185152 25.3371967,30.8192672 Z"
                      id="Exclusion_1"
                    ></path>
                  </g>
                </g>
              </svg>
            </div>
          </div>
          {this.renderBody()}
        </div>
      </>
    )
  }
}

function mapStateToProps({ error, tabs, recording, settings }) {
  const isRecording = ![RecordingState.INIT, RecordingState.STOPPED].includes(recording.state)

  const isPaused = recording.state === RecordingState.PAUSED

  return {
    ...settings,
    recordingState: recording.state,
    isRecording,
    isPaused,
    openIn: settings.openIn,
    error: error.details,
    recordedTab: recording.recordedTab,
    recordingInAnotherTab: !isSameTab(tabs.activeTab, recording.recordedTab),
    recordingInAnotherWindow: !isSameWindow(tabs.activeTab, recording.recordedTab),
    canRecordTab: tabs.activeTab !== null,
  }
}

export default connect(mapStateToProps)(Popup)
