import { tabs } from '../../../platform/async'
import { action } from '../../../store/utils'
import { ACTIVE_TAB_CHANGED } from './types'

export const viewTab = (tab) => async (dispatch) => {
  await tabs.update(tab.tabId, { active: true })

  dispatch(activeTabChanged({ tab }))
}

export const activeTabChanged = action(ACTIVE_TAB_CHANGED)
