import { CONFIGURE } from './types'

import { DEBUGGER, supportsRecorder } from '../../../recording/recorders/types'
import { INITIALIZE } from '../recording/types'
import { OPEN_IN_NEW_TAB } from '../../../settings'

const INITIAL_STATE = {
  downloadFile: false,
  clearCache: true,
  correlateRequests: supportsRecorder(DEBUGGER),
  openIn: OPEN_IN_NEW_TAB,
}

export const settings = (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case INITIALIZE:
      return {
        ...state,
        openIn: payload.openIn,
      }

    case CONFIGURE:
      return {
        ...state,
        ...payload,
      }

    default:
      return state
  }
}
