import React from 'react'
import styled, { keyframes } from 'styled-components'

const StrokeAnimation = keyframes`
  0% {
    stroke-dasharray: 1px, 200px;
    stroke-dashoffset: 0px;
  }

  50% {
    stroke-dasharray: 100px, 200px;
    stroke-dashoffset: -15px;
  }

  100% {
    stroke-dasharray: 100px, 200px;
    stroke-dashoffset: -125px;
  }
`

const SpinningAnimation = keyframes`
  from {
    transform: rotate(0deg)
  }

  to {
    transform: rotate(360deg)
  }
`

const SpinnerStyle = styled.div`
  display: flex;

  align-items: center;
  justify-content: center;

  position: absolute;

  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #bbb;
  opacity: 0.4;

  > .icon {
    display: block;

    width: 40px;
    height: 40px;

    animation: ${SpinningAnimation} 2s linear infinite;

    color: #7d64ff;

    > .stroke {
      stroke: currentColor;
      animation: ${StrokeAnimation} 1.4s ease-in-out infinite;
      stroke-dasharray: 80px, 200px;
      stroke-dashoffset: 0px;
    }
  }
`

export const Spinner = () => {
  return (
    <SpinnerStyle>
      <svg className="icon" viewBox="22 22 44 44">
        <circle className="stroke" cx="44" cy="44" r="20.2" fill="none" strokeWidth="3.6"></circle>
      </svg>
    </SpinnerStyle>
  )
}
