import {
  RecordingState,
  INITIALIZE,
  START_RECORDING,
  RECORDING_STARTED,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  STOP_RECORDING,
  RECORDING_STOPPED,
} from './types'
import { ERROR } from '../error/types'

const INITIAL_STATE = {
  state: RecordingState.INIT,
  activeTab: null,
  recordedTab: null,
}

export const recording = (state = INITIAL_STATE, { type, payload }) => {
  switch (type) {
    case INITIALIZE:
      return {
        ...state,
        state: payload.state,
        recordedTab: payload.recordedTab,
      }

    case ERROR:
      return {
        ...state,
        state: RecordingState.STOPPED,
        recordedTab: null,
      }

    case START_RECORDING:
      return {
        ...state,
        state: RecordingState.STARTING,
        recordedTab: payload.tab,
      }

    case RECORDING_STARTED:
      return {
        ...state,
        state: RecordingState.STARTED,
      }

    case PAUSE_RECORDING:
      return {
        ...state,
        state: RecordingState.PAUSED,
      }

    case RESUME_RECORDING:
      return {
        ...state,
        state: RecordingState.STARTED,
      }

    case STOP_RECORDING:
      return {
        ...state,
        state: RecordingState.STOPPING,
      }

    case RECORDING_STOPPED:
      return {
        ...state,
        state: RecordingState.STOPPED,
        recordedTab: null,
      }

    default:
      return state
  }
}
