import { runtime } from '../../platform'

import {
  REQUEST_MSG,
  TRIGGER_MSG,
  GET_RECORDING_STATE,
  START_RECORDING,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  STOP_RECORDING,
  LIST_RECORDINGS,
  DOWNLOAD_RECORDING,
  OPEN_IN_CLOUD,
  RESET_RECORDING,
} from './types'

class ConnectionError extends Error {
  constructor(payload) {
    super(`Port was disconnected while waiting on request of type ${payload.type}`)

    this.payload = payload
  }
}

class RemoteError extends Error {
  constructor(error) {
    super(`Error thrown in background process: ${error.message}`)
  }
}

export class BackgroundClient {
  port = null
  requestId = 0

  constructor(name) {
    this.name = name
  }

  nextRequestId() {
    this.requestId = this.requestId + 1

    return this.requestId
  }

  connect() {
    if (this.port == null) {
      this.port = runtime.connect({ name: 'options-client' })
      this.port.onDisconnect.addListener(this.onDisconnect)
    }

    return this.port
  }

  onDisconnect = () => {
    this.port = null
  }

  send(msg) {
    this.port.postMessage({ type: TRIGGER_MSG, payload: msg })
  }

  request(msg) {
    const envelope = {
      type: REQUEST_MSG,
      requestId: this.nextRequestId(),
      payload: msg,
    }

    return new Promise((resolve, reject) => {
      const port = this.connect()

      const onDisconnect = () => {
        reject(new ConnectionError(envelope.payload))
      }

      const waitForResponse = (msg) => {
        if (msg.requestId !== envelope.requestId) {
          return
        }

        if (!msg.succeeded) {
          return reject(new RemoteError(msg.error))
        }

        port.onMessage.removeListener(waitForResponse)
        port.onDisconnect.removeListener(onDisconnect)

        resolve(msg.response)
      }

      port.onMessage.addListener(waitForResponse)
      port.onDisconnect.addListener(onDisconnect)

      port.postMessage(envelope)
    })
  }

  getRecordingState() {
    return this.request({
      type: GET_RECORDING_STATE,
    })
  }

  startRecording(opts) {
    return this.request({
      type: START_RECORDING,
      opts,
    })
  }

  pauseRecording() {
    return this.request({
      type: PAUSE_RECORDING,
    })
  }

  resumeRecording() {
    return this.request({
      type: RESUME_RECORDING,
    })
  }

  stopRecording() {
    return this.request({
      type: STOP_RECORDING,
    })
  }

  resetRecording() {
    return this.request({
      type: RESET_RECORDING,
    })
  }

  listRecordings() {
    return this.request({
      type: LIST_RECORDINGS,
    })
  }

  download(index) {
    this.send({
      type: DOWNLOAD_RECORDING,
      index,
    })
  }

  openInCloud(index, openIn) {
    this.send({
      type: OPEN_IN_CLOUD,
      index,
      openIn,
    })
  }
}
