import { client } from '../../client'

import { storage, callAsync } from '../../../platform'
import { tabs } from '../../../platform/async'

import { toTabRef } from '../../../utils/tabs'

import { error } from '../error/actions'
import { activeTabChanged } from '../tabs/actions'

import {
  INITIALIZE,
  START_RECORDING,
  RECORDING_STARTED,
  PAUSE_RECORDING,
  RESUME_RECORDING,
  STOP_RECORDING,
  RECORDING_STOPPED,
} from './types'

import { tryAction } from '../error/utils'
import { DEFAULT_SETTINGS, OPEN_IN_EXISTING_TAB, OPEN_IN_NEW_TAB } from '../../../settings'

const getActiveTab = () =>
  tabs.query({ active: true, currentWindow: true }).then((tabs) => toTabRef(tabs[0]))

const getSettings = () => callAsync(storage.local.get.bind(storage.local), DEFAULT_SETTINGS)

export const initialize = () => async (dispatch) => {
  const [activeTab, state, settings] = await Promise.all([
    getActiveTab(),
    client.getRecordingState(),
    getSettings(),
  ])

  console.log('loaded settings', settings)

  dispatch(activeTabChanged({ tab: activeTab }))

  dispatch({
    type: INITIALIZE,
    payload: {
      state: state.state,
      recordedTab: state.recordedTab,
      openIn: settings.reuseExistingTab ? OPEN_IN_EXISTING_TAB : OPEN_IN_NEW_TAB,
    },
  })
}

export const startRecording = (opts) =>
  tryAction(async (dispatch) => {
    const tab = await getActiveTab()

    if (tab === null) {
      return dispatch(
        error({
          title: 'Failed to start recording',
          message: 'The active tab cannot be recorded.',
        })
      )
    }

    dispatch({ type: START_RECORDING, payload: { tab } })

    await client.startRecording({ ...opts, tab })

    dispatch({ type: RECORDING_STARTED })
  })

export const pauseRecording = () =>
  tryAction(async (dispatch) => {
    dispatch({ type: PAUSE_RECORDING })

    await client.pauseRecording()
  })

export const resumeRecording = () =>
  tryAction(async (dispatch) => {
    dispatch({ type: RESUME_RECORDING })

    await client.resumeRecording()
  })

export const stopRecording = () =>
  tryAction(async (dispatch) => {
    dispatch({ type: STOP_RECORDING })

    await client.stopRecording()

    dispatch({ type: RECORDING_STOPPED })
  })

export const resetRecording = () =>
  tryAction(async (dispatch) => {
    dispatch({ type: STOP_RECORDING })

    await client.resetRecording()

    dispatch({ type: RECORDING_STOPPED })
  })
