export const createStorage = (impl = sessionStorage) => {
  return {
    load: (key, defaultState) => {
      const value = impl.getItem(key)

      if (value === null) {
        return defaultState
      }

      return JSON.parse(value)
    },
    save: (key, state) => {
      impl.setItem(key, JSON.stringify(state))
    },
    clear: () => {
      impl.clear()
    },
  }
}
