import { error } from './actions'

export const tryAction = (action) => async (dispatch, ...args) => {
  try {
    return await action(dispatch, ...args)
  } catch (err) {
    dispatch(
      error({
        title: 'An unexpected error occurred',
        description:
          'Could not perform the given action because the background process threw an error.',
        details: err.message,
      })
    )

    throw err
  }
}
