import { createGlobalStyle, css, keyframes } from 'styled-components'

const fadeInAnimation = keyframes`
  0% {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
`

const recordingBlinkAnimation = keyframes`
  0% {
    background-color: #e03e2f;
    box-shadow: 0 0 10px #e03e2f;
  }

  50% {
    background-color: #fff;
    box-shadow: none;
  }

  to {
    background-color: #e03e2f;
    box-shadow: 0 0 10px #e03e2f;
  }
`

const baseStyles = css`
  #unfocus-me {
    outline: 0;
  }

  body {
    font-size: 12px;
    margin: 0;
    color: #3c4858;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue',
      Arial, sans-serif;
  }

  p,
  h1,
  h2,
  h3 {
    margin-top: 0;
  }

  p {
    margin-bottom: 15px;
  }

  h2 {
    font-weight: 400;
  }

  a:not(.btn) {
    color: #007bff;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      color: #3c3c64;
    }
  }
`
const layoutStyles = css`
  .layout {
    width: 300px;
    max-height: 500px;
    min-height: 200px;
    .layout__header {
      padding: 10px 15px;
      background: #f1f1f1;
      display: flex;
      flex-direction: row;
      align-items: center;
    }

    .layout__body {
      animation: ${fadeInAnimation} linear 0.1s;
      padding: 20px 30px;
      margin: 0 auto;
    }

    &.layout--beta {
      position: relative;
      &:before {
        content: 'BETA';
        position: absolute;
        top: 13px;
        right: -18px;
        transform: rotate(45deg);
        width: 80px;
        text-align: center;
        height: 17px;
        line-height: 17px;
        background-color: #535353;
        color: #ddd;
      }
    }
  }

  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    svg,
    img {
      height: 22px;
    }
  }

  .idle-display {
    text-align: center;
    margin-bottom: 20px;
    p {
      color: #8492a6;
      font-size: 12px;
    }
    img {
      width: 70px;
    }

    .idle-display__border {
      border-top: 1px solid #efefef;
      max-width: 220px;
      margin: 0 auto;
      position: relative;
      top: -8px;
      z-index: -1;
    }
  }

  .options {
    margin-bottom: 15px;
  }
`
const formsStyles = css`
  .switch {
    display: flex;
    align-items: center;
    height: 30px;

    ::selection {
      background-color: transparent;
    }

    .switch__label {
      cursor: pointer;
      flex: 1;
      transition: color 0.2s;
      p {
        font-weight: 500;
        display: inline-block;
        margin: 0;
        padding: 5px 0;
      }
    }

    .switch__control {
      width: 40px;
      position: relative;
      width: 34px;
      -webkit-appearance: none;
      padding: 0;
      border: 0;
      outline: none;
      cursor: pointer;
      background: transparent;
    }

    .switch__control:focus .switch__thumb {
      box-shadow: 0 0 0px 2px rgba(125, 100, 255, 0.29);
    }

    .switch__thumb,
    .switch__track {
      transition: background-color 0.2s ease, transform 0.2s ease;
    }

    .switch__track {
      height: 14px;
      background-color: #ddd;
      border-radius: 8px;
    }

    .switch__thumb {
      position: absolute;
      left: 0;
      width: 20px;
      height: 20px;
      background-color: #fafafa;
      border-radius: 50%;
      top: 50%;
      transform: translate(0, -50%);
      box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.14), 0 2px 2px 0 rgba(0, 0, 0, 0.098),
        0 1px 5px 0 rgba(0, 0, 0, 0.084);
    }

    &.switch--off {
      .switch__label {
        color: #929292;
      }
    }
    &.switch--on {
      .switch__thumb {
        background-color: #7d64ff;
        transform: translate(80%, -50%);
      }
    }
  }

  .radiobox {
    cursor: pointer;
    margin-bottom: 5px;

    input {
      margin-right: 5px;
    }

    input,
    label {
      cursor: pointer;
    }
    ::selection {
      background-color: transparent;
    }
  }
`
const buttonsStyles = css`
  .btn {
    font-size: 12px;
    -webkit-appearance: none;
    border-radius: 0px;
    padding: 6px 12px;
    min-width: 74px;
    cursor: pointer;
    transition: background-color 0.1s;
    border: 1px solid #d4d4d4;

    &:focus {
      box-shadow: 0 0 0px 2px #8fc1ded9;
    }

    &:active,
    &:focus {
      outline: 0;
    }

    &:hover {
      background-color: #eee;
    }
  }

  .btn-row {
    display: flex;

    .btn {
      flex: 1;
    }

    .btn:focus {
      border-color: #8fc1ded9;
      box-shadow: inset 0 0 0px 1px #8fc1ded9;
    }

    .btn:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
    .btn:nth-child(2) {
      border-radius: 0;
      border-left: 0;
      border-right: 0;
    }
    .btn:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }

  .btn--record {
    background-color: #d9534f;
    border-color: #d43f3a;
    color: #fff;
    width: 100%;
    &:hover {
      background-color: #d2322d;
      border-color: #ac2925;
    }
    span:before {
      content: '';
      width: 10px;
      height: 10px;
      border-radius: 50%;
      display: inline-block;
      margin-right: 5px;
      background-color: #fff;
    }
    &.btn--record--active span:before {
      animation: ${recordingBlinkAnimation} 0.6s infinite;
    }
  }

  .btn--history {
    background-color: transparent;
    padding: 0;
    border: 0;
    display: block;
    margin: 12px auto 5px;
    color: #1769aa;
    &:hover {
      background: transparent;
      color: #104976;
    }
  }
`
const messagesStyles = css`
  .message {
    padding: 10px;
    border-radius: 4px;
    animation: fade-in ease 0.2s;
    background-color: #f9f9f9;
    border: 1px solid #d0d0d0;
    margin-bottom: 20px;
    .message__title {
      margin-bottom: 5px;
    }
    .message__description {
      margin-bottom: 0;
    }

    &.message--info {
      background: linear-gradient(#f5fbff, #f1f9ff);
      border-color: #9cc0e6;
      color: #2b79ab;
    }

    &.message--alert {
      border-color: #ffa39e;
      background-color: #fff1f0;
    }
  }
`

export const GlobalStyles = createGlobalStyle`
  ${baseStyles}
  ${layoutStyles}
  ${formsStyles}
  ${messagesStyles}
  ${buttonsStyles}
`
